import React from 'react';
import Masonry from 'react-masonry-css';
// import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';

import Container from '../../components/Layout/Container';
import FilterSection from '../../components/Filters/FilterSection';
import ProductCard from '../../components/Cards/ProductCard';
import LoadMoreButton from '../../components/Buttons/LoadMoreButton';

import { products } from '../../constants/dummy.json';
import SubTitle1 from '../../components/Typography/Subtitle1';

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: 80,
    paddingBottom: 136,
  },
  masonry: {
    display: 'flex',
    margin: theme.spacing(0, -1.75),
  },
  gridColumn: {
    margin: theme.spacing(0, 2),
  },
  topFilter: {
    borderBottom: `1px solid ${theme.palette.divider}`,
    padding: theme.spacing(3, 0),
    marginBottom: theme.spacing(5),
  },
  products: {
    paddingLeft: 7,
    [theme.breakpoints.down('sm')]: {
      paddingLeft: 0,
    },
  },
  productWrapper: {
    padding: theme.spacing(1.5, 1.5, 2.75),
    marginBottom: theme.spacing(4),
    borderRadius: theme.shape.cardBorderRadius,
    backgroundColor: theme.palette.type === 'light' ? theme.palette.surface[0] : theme.palette.surface[1],
    boxShadow: '0 25px 28px 10px #1111',
  },
  loadBtn: {
    display: 'flex',
    justifyContent: 'center',
    marginTop: -1,
  },
}));

const Explore = () => {
  const classes = useStyles();
  const breakpointColumnsObj = {
    default: 4,
    900: 4,
    700: 3,
    500: 1,
  };

  return (
    <div className={classes.root}>
      <Container>
        <SubTitle1 color="primary">Filter</SubTitle1>
        <div className={classes.topFilter}>
          <FilterSection />
        </div>

        <Masonry
          breakpointCols={breakpointColumnsObj}
          className={classes.masonry}
          columnClassName={classes.gridColumn}
        >
          {products.slice(0, 8).map((product) => (
            <div
              key={product.id}
              className={classes.productWrapper}
            >
              <ProductCard product={product} />
            </div>
          ))}
        </Masonry>

        <div className={classes.loadBtn}>
          <LoadMoreButton />
        </div>
      </Container>
    </div>
  );
};

Explore.propTypes = {
};

Explore.defaultProps = {
};

export default Explore;
