import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import Container from '../../components/Layout/Container';
import PageTitle from '../../components/Typography/PageTitle';
import ProductSection from '../../components/ProductSection';
import LoadMoreButton from '../../components/Buttons/LoadMoreButton';

import { products } from '../../constants/dummy.json';
import FeatureSection from '../../components/FeatureSection';
import SignupAction from '../../components/SignupAction';

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: theme.spacing(5),
    paddingBottom: theme.spacing(5),
    [theme.breakpoints.down('xs')]: {
      paddingTop: 76,
      textAlign: 'center',
    },
  },
  top: {
    display: 'flex',
    justifyContent: 'center',
    padding: theme.spacing(6, 0, 2),
  },
  title: {
    [theme.breakpoints.down('xs')]: {
      lineHeight: '47px',
    },
  },
  section: {
    paddingTop: theme.spacing(8),
  },
  bottom: {
    padding: theme.spacing(8, 0),
    display: 'flex',
    justifyContent: 'center',
  },
}));

const Home = () => {
  const classes = useStyles();

  return (
    <Container className={classes.root}>
      <div className={classes.top}>
        <PageTitle className={classes.title}>A creator economy.</PageTitle>
      </div>
      <div className={classes.section}>
        <FeatureSection />
      </div>
      <div className={classes.section}>
        <ProductSection
          title="Auctions"
          resources={products.slice(0, 4)}
        />
      </div>
      <div className={classes.section}>
        <br />
        <SignupAction />
      </div>
      <div className={classes.section}>
        <ProductSection
          title="Recently added"
          resources={products.slice(4, 8)}
        />
      </div>
      <div className={classes.section}>
        <ProductSection
          title="Buy now"
          resources={products.slice(5, 9)}
        />
      </div>

      <div className={classes.bottom}>
        <LoadMoreButton />
      </div>
    </Container>
  );
};

export default Home;
