import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import LoyaltyIcon from '@material-ui/icons/Loyalty';

import ImageWrapper from '../ImageWrapper';
import OutlinedButton from '../Buttons/OutlinedButton';
import Body2 from '../Typography/Body2';
import { resourcePath } from '../../constants/config';

const useStyles = makeStyles((theme) => ({
  root: {
  },
  productWrapper: {
    width: '100%',
    height: 500,
    borderRadius: theme.shape.cardBorderRadius,
    borderBottomRightRadius: 0,
    borderBottomLeftRadius: 0,
    background: theme.palette.surface[1],
  },
  offers: {
    minHeight: 250,
    borderRadius: theme.shape.cardBorderRadius,
    borderTopRightRadius: 0,
    borderTopLeftRadius: 0,
    border: `1px solid ${theme.palette.surface[2]}`,
    padding: theme.spacing(3),
    display: 'flex',
    flexDirection: 'column',
  },
  icon: {
    color: theme.palette.text.secondary,
    marginTop: theme.spacing(2),
  },
  content: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    flex: 1,
  },
  bottom: {
    display: 'flex',
    justifyContent: 'flex-end',
    '& button': {
      width: theme.spacing(18),
    },
  },
}));

const ProductImage = () => {
  const classes = useStyles();

  return (
    <div>
      <ImageWrapper
        className={classes.productWrapper}
        content={`${resourcePath.product}product_02.png`}
      />
      <div className={classes.offers}>
        <div className={classes.content}>
          <Body2 color="secondary" component="span">
            No Offers yet
          </Body2>
          <LoyaltyIcon className={classes.icon} />
        </div>
        <div className={classes.bottom}>
          <OutlinedButton label="Make an Offer" />
        </div>
      </div>
    </div>
  );
};

export default ProductImage;
