import React from 'react';
// import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import UpdateIcon from '@material-ui/icons/Update';
import VisibilityIcon from '@material-ui/icons/Visibility';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';

import Container from '../../components/Layout/Container';
import Title from '../../components/Typography/Title';
import Body1 from '../../components/Typography/Body1';
import Body2 from '../../components/Typography/Body2';
import ProductActionCard from '../../components/Cards/ProductActionCard';
import ProductImage from '../../components/ProductDetail/ProductImage';
import ChainInfo from '../../components/ProductDetail/ChainInfo';
import TradingHistory from '../../components/ProductDetail/TradingHistory';

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: 97,
    paddingBottom: 96,
  },
  icon: {
    color: theme.palette.text.primary,
    marginRight: theme.spacing(2),
  },
  title: {
    margin: theme.spacing(3, 0),
  },
  tag: {
    display: 'flex',
    alignItems: 'center',
  },
  row: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: theme.spacing(1, 0),
  },
  status: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    background: theme.palette.surface[2],
    padding: theme.spacing(1.5, 0),
    borderRadius: theme.shape.borderRadius,
  },
  description: {
    paddingTop: theme.spacing(4),
  },
  btnGroup: {
    '& .MuiButton-outlined': {
      border: `1px solid ${theme.palette.surface[3]}`,
    },
  },
}));

const ProductDetail = () => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Container>
        <Grid container spacing={6}>
          <Grid item md={6} sm={6} xs={12}>
            <ProductImage />
          </Grid>

          <Grid item md={6} sm={6} xs={12}>
            <div className={classes.status}>
              <UpdateIcon className={classes.icon} />
              <Body1>
                Sales ends today in 00:00:00
              </Body1>
            </div>
            <Title className={classes.title}>The amazing art</Title>
            <Body1 color="secondary" component="span">
              CREATED BY: artistname
            </Body1>
            <div className={classes.row}>
              <div className={classes.tag}>
                <VisibilityIcon className={classes.icon} />
                <Body2>14 Views</Body2>
              </div>

              <ButtonGroup className={classes.btnGroup}>
                <Button>Refresh</Button>
                <Button>Share</Button>
                <Button>Report</Button>
              </ButtonGroup>
            </div>

            <ProductActionCard price={1232} />

            <Divider />

            <Body1 className={classes.description} color="secondary">
              Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
              tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
              quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
              consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse
              cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non
              proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
            </Body1>
          </Grid>

          <Grid item md={6} sm={6} xs={12}>
            <TradingHistory />
          </Grid>

          <Grid item md={6} sm={6} xs={12}>
            <ChainInfo />
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

ProductDetail.propTypes = {
};

ProductDetail.defaultProps = {
};

export default ProductDetail;
