import React from 'react';
import { Grid, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyle = makeStyles((theme) => ({
  root: {
    paddingTop: theme.spacing(5),
    paddingBottom: theme.spacing(5),
    [theme.breakpoints.down('xs')]: {
      paddingTop: 76,
      textAlign: 'center',
    },
  },
  img_container: {
    paddingRight: '2.5em',
  },
  img_feature: {
    width: '100%',
    filter: 'drop-shadow(rgba(0, 0, 0, 0.25) 0px 20px 20px)',
    borderRadius: '10px',
  },
  user: {
    boxShadow: '0px 10px 20px rgba(0,0,0,0.05)',
    padding: '1em',
    fontSize: theme.typography.subtitle2.fontSize,
  },
  title: {
    fontSize: '3em',
    marginBottom: '.5em',
    color: theme.palette.text.primary,
  },
  sub_title: {
    color: theme.palette.text.primary,
  },
  sub_title_3: {
    color: theme.palette.text.secondary,
  },
  price: {
    fontSize: '2em',
    marginBottom: '.3em',
    color: theme.palette.text.primary,
  },
  action: {
    display: 'flex',
    padding: '1em 1em 1em 0',
  },
  action_btn_first: {
    flex: 2,
    backgroundColor: '#3772ff',
    marginRight: 16,
    color: 'white',
    padding: '2em',
    borderRadius: 4,
    fontWeight: 'bold',
  },
  action_btn_2: {
    flex: 1,
    padding: '2em',
    borderRadius: 4,
    fontWeight: 'bold',
  },
}));

const FeatureSection = () => {
  const classes = useStyle();
  return (
    <Grid className={classes.root} container>
      <Grid
        sm={12}
        md={6}
        lg={6}
        item
      >
        <img src="/assets/images/products/product_03.png" alt="asd" className={classes.img_feature} />
      </Grid>
      <Grid
        sm={12}
        md={6}
        lg={6}
        item
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <div style={{
          flex: 1,
          paddingLeft: '4em',
        }}
        >
          <Button className={classes.user}>
            @newrafeel
          </Button>
          <h2 className={classes.title}>Double Room</h2>
          <h3 className={classes.sub_title}>Reserve Price</h3>
          <h1 className={classes.price}>5.00 ETH</h1>
          <p className={classes.sub_title_3}>
            $12,857.25
          </p>
          <div className={classes.action}>
            <Button className={classes.action_btn_first}>
              Place a bid
            </Button>
            <Button className={classes.action_btn_2}>
              View artwork
            </Button>
          </div>
        </div>
      </Grid>
    </Grid>
  );
};

export default FeatureSection;
