import React from 'react';
// import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Hidden from '@material-ui/core/Hidden';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';

import Container from '../../components/Layout/Container';
import PageTitle from '../../components/Typography/PageTitle';
import SubTitle1 from '../../components/Typography/Subtitle1';
import FilledButton from '../../components/Buttons/FilledButton';
import LabelWithComment from '../../components/Widgets/LabelWIthComment';
import UploadFile from '../../components/Forms/UploadFile';
import InputField from '../../components/Forms/InputField';
import SelectField from '../../components/Forms/SelectField';
import ProductCard from '../../components/Cards/ProductCard';
import Pane from '../../components/Pane';
import LoadingText from '../../components/Widgets/LoadingText';

import { products } from '../../constants/dummy.json';
import { creatorOptions } from '../../constants/filter';

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: 128,
    paddingBottom: 127,
    [theme.breakpoints.down('sm')]: {
      paddingTop: 80,
      paddingBottom: 112,
    },
  },
  title: {
    marginBottom: 45,
  },
  label: {
    fontWeight: 500,
  },
  formWrapper: {
    marginTop: 28,
    width: '100%',
  },
  formGroup: {
    paddingTop: 3,
  },
  preview: {
    background: 'transparent',
    borderRadius: theme.shape.cardBorderRadius,
    padding: '46px 47px',
    boxShadow: '0 37px 28px 10px #1111',
    [theme.breakpoints.down('sm')]: {
      padding: 21,
    },
  },
  product: {
    background: 'transparent',
    marginBottom: 34,
  },
  previewLabel: {
    color: theme.palette.text.primary,
    marginBottom: 25,
  },
  clear: {
    display: 'flex',
    alignItems: 'center',
    color: theme.palette.text.secondary,
    fontWeight: 'bold',
    '& svg': {
      marginRight: 6,
    },
    '&:hover': {
      cursor: 'pointer',
      color: theme.palette.text.primary,
    },
  },
  upload: {
    marginBottom: 40,
  },
  buttonGroup: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  createBtn: {
    [theme.breakpoints.down('xs')]: {
      order: 2,
      marginTop: 10,
    },
  },
}));

const Create = () => {
  const classes = useStyles();
  const theme = useTheme();
  const isMobileOrTablet = useMediaQuery(`(max-width: ${theme.breakpoints.values.xs}px)`);

  return (
    <div className={classes.root}>
      <Container>
        <Grid
          container
          justify="space-between"
          spacing={4}
        >
          <Grid item md={7} xs={12}>
            <PageTitle className={classes.title}>
              Create a new NFT
            </PageTitle>

            <Box marginBottom={5}>
              <InputField
                wrapperClass={classes.formWrapper}
                label="Name"
                placeholder="e.g.“Best NFT Product”"
              />
              <InputField
                wrapperClass={classes.formWrapper}
                label="Description"
                isMulti
                placeholder="e.g.“After purchasing you will able to received the logo...”"
              />

              <Box marginTop={5}>
                <LabelWithComment comment="Drag or choose your file to upload">
                  Upload file
                </LabelWithComment>
                <UploadFile />
              </Box>

              <Grid
                container
                spacing={isMobileOrTablet ? 0 : 3}
                className={classes.formGroup}
              >
                <Grid item sm={4} xs={12}>
                  <SelectField
                    className={classes.formWrapper}
                    label="Royalties (%)"
                    iconBorder={false}
                    options={creatorOptions}
                    value={creatorOptions[0].key}
                  />
                </Grid>
                <Grid item sm={4} xs={12}>
                  <InputField
                    wrapperClass={classes.formWrapper}
                    label="To Mint"
                    placeholder="e.g. 2,000"
                    icon={<AttachMoneyIcon />}
                  />
                </Grid>
                <Grid item sm={4} xs={12}>
                  <InputField
                    wrapperClass={classes.formWrapper}
                    label="Max supply"
                    placeholder="e.g. 1,200"
                    icon={<AttachMoneyIcon />}
                  />
                </Grid>
              </Grid>

              <SelectField
                className={classes.formWrapper}
                label="Attributes/MetaData"
                iconBorder={false}
                options={creatorOptions}
                value={creatorOptions[0].key}
              />
              <InputField
                wrapperClass={classes.formWrapper}
                label="Buy now price"
                placeholder="e.g.“2,500,330”"
                icon={<AttachMoneyIcon />}
              />
              <InputField
                wrapperClass={classes.formWrapper}
                label="Auction Details"
                isMulti
                placeholder="e.g.“Auction Detail...”"
              />
            </Box>

            <div className={classes.buttonGroup}>
              <FilledButton
                size="large"
                label="Create item"
                icon={<ArrowRightAltIcon />}
                className={classes.createBtn}
              />
              <Hidden xsDown>
                <Box marginRight={3}>
                  <LoadingText isLoading label="Auto saving" />
                </Box>
              </Hidden>
            </div>
          </Grid>
          <Grid item md={4} xs={12}>
            <Pane className={classes.preview}>
              <SubTitle1 className={classes.previewLabel}>Preview</SubTitle1>
              <ProductCard
                className={classes.product}
                product={products[0]}
              />

              <div className={classes.clear}>
                <HighlightOffIcon />
                Clear all
              </div>
            </Pane>
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

Create.propTypes = {
};

Create.defaultProps = {
};

export default Create;
